@import url(//db.onlinewebfonts.com/c/1e7e97d0da36ea1b00a66348a43dfb26?family=Proxima+Nova);
.App{display:flex;justify-content:center;align-items:center;height:100%;width:100%;text-align:center;background-color:#f5f9ff;padding:131px 0 30px}.component{background-color:#fff;height:590px;width:400px;display:flex;flex-direction:column;align-items:center;font-size:calc(10px + 2vmin);border:1px solid #e8f1fb;font-family:"Proxima Nova",sans-serif}.component__header{width:100%;padding:21px 0 0 29px;height:80px;align-self:flex-start;border-bottom:1px solid #e8f1fb}.component__header--title{text-align:start;font-weight:bold;font-size:20px;color:#1a2533}.component__header--text{text-align:start;font-size:12px}.component__body{box-sizing:padding-box;border-top:19px solid #fff;border-right:20px solid #fff;border-bottom:21px solid #fff;border-left:20px solid #fff;width:100%;height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;font-size:12px}.container{display:flex;flex-direction:column;align-items:center}.container__logo{width:80px;height:80px;position:relative;margin-bottom:9px}.container__logo--input{display:none}.container__logo--image{height:100%;width:100%;border-radius:50%}.container__logo--loader{position:absolute;top:0;left:0;width:100%;height:100%;border:1px solid #4991e5;border-radius:50%;border-bottom-color:transparent;-webkit-animation:anim 1s 0s infinite linear;animation:anim 1s 0s infinite linear}.container--separator{margin:8px 0 4px;line-height:1em}.container--upload{color:#4991e5;cursor:pointer}@-webkit-keyframes anim{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes anim{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}body{font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}*{outline:none;margin:0;padding:0;box-sizing:border-box}
